.title {
    font-size: 43px;
    text-align: center;
    margin-bottom: 50px;
}

.list {
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 30px;
}

.item {
    width: 360px;
    height: 480px;
    display: flex;

    margin-bottom: 50px;
}


.link{
    display: block;
    color: inherit;
    text-decoration: none;
}

.link:hover,
.link:focus {
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12),
        0px 4px 4px rgba(0, 0, 0, 0.06),
        1px 4px 6px rgba(0, 0, 0, 0.16);
}

.img{
    height: 100%;
}

.subtitle{
    margin-bottom: 4px;

    text-align: center;

    font-weight: 700;
    font-size: 18px;
    line-height: calc(36 / 18);
    letter-spacing: 0.06em;
}